.work {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.works-title {
    font-weight: bold;
    margin-top: 7.5vh;
    font-family: 'Ubuntu', sans-serif;
    -webkit-text-stroke: 2px #1d1d1d;
    -webkit-text-fill-color: transparent;
    font-size: var(--font-80);
    line-height: 4.5vw;
    font-weight: bold;
    letter-spacing: 5px;
    transition: all 0.3s ease-in-out;

}

.works {
    width: 73%;
    margin: auto;
    margin: 3.25vh 0 6.75vh 0;
    display: flex;
    flex-wrap: wrap;
    column-gap: 2.15vw;
    row-gap: 2.15vw;
}

.work-container {
    position: relative;
    flex: 1 1 25%;
    height: 14.25vw;
    max-width: calc(33% - 1.5vw);
    font-weight: 400;
    display: flex;
    flex-direction: column;
    font-size: var(--font-20);
}

.work-container:hover .work-body>img {
    opacity: 0;
    visibility: hidden;
}

.work-container:hover .work-header {
    background: #1d1d1d;
    color: #fff;
}

.work-header {
    width: fit-content;
    border-top-left-radius: 15px;
    border-bottom-right-radius: 15px;
    border: 2px solid #1d1d1d;
    padding: 0.3vw 1vw;
    margin-top: 0px;
    margin-left: 1px;
    background: #fff;
    font-size: var(--font-12);
    font-weight: 500;
    color: #1d1d1d;
    transition: all 0.3s ease-in-out;
    z-index: 200;
}

.work-body {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    border: 2px solid #1d1d1d;
    border-radius: 15px;
    overflow: hidden;
    height: 100%;
}

.work-body img {
    width: 100%;
    align-self: normal;
    transition: all 0.3s ease-in-out;
    z-index: 100;
    visibility: visible;
    opacity: 1;
}

.details {
    position: absolute;
    text-align: justify;
    padding: 0 2.15vw;
    hyphens: auto;
    font-size: var(--font-14);
}

.details-actions {
    position: absolute;
    bottom: 1.5vw;
    display: flex;
    column-gap: 1.5vw;
}


.details-actions button {
    flex: 1 1;
    width: 100%;
    border: 2px solid #1d1d1d;
    background: #fff;
    padding: 0.25vw 0.75vw;
    border-radius: 100px;
    font-weight: 500;
    font-size: var(--font-12);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.details-actions button:hover {
    background: #1d1d1d;
    color: #fff;
}

.work > .action-buttons button {
    border-color: #1d1d1d!important;
}


@media only screen and (max-width: 600px) {

    :root {
        --font-12: 3vw;
        --font-14: 3.5vw;
        --font-16: 4.125vw;
        --font-18: 4.75vw;
        --font-20: 5.125vw;
        --font-40: 9vw;
        --font-48: 11.75vw;
    }

    .works-title {
        margin-top: 15vw;
        font-size: var(--font-48);
    }

    .works {
        width: 100%;
        padding: 0 7.5vw 7.5vw 7.5vw;
        margin: 0;
        flex-direction: column;
        justify-content: unset;
        flex-wrap: unset;
        row-gap: 7.5vw;
    }

    .work-container {
        max-width: unset;
        height: auto;
    }

    .work-header {
        padding: 1.5vw 5vw;
    }

    .work-body {
        margin-top: -7.5vw;
        position: relative;
    }

    .details-actions button {
        padding: 1vw 3vw;
    }

}